import React, { Component, Fragment } from "react";
import { Row, Col, Button, Table, Input } from 'reactstrap';
import { _isEmpty, _Map, _debounce, _isUndefined } from '../../helper/HelperFunction';
import * as NC from "../../helper/NetworkingConstants";
import WSManager from "../../helper/WSManager";
import { notify } from 'react-notify-toast';
import Select from 'react-select';
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";
import Pagination from "react-js-pagination";
import moment from 'moment';
import Loader from '../../components/Loader';
import HF from "../../helper/HelperFunction";
import { MODULE_NOT_ENABLE } from "../../helper/Message";
const ReportType = [
    { "label": "GST Report", "value": "tax_invoice" },
    { "label": "TDS Report", "value": "tds_invoice" },
]
const StateType = [
    { "label": "All", "value": "" },
    { "label": "Intra State", "value": "intra_state" },
    { "label": "Inter State", "value": "inter_state" }
]

export default class GSTReports extends Component {
    constructor(props) {
        super(props)
        this.state = {
            PERPAGE: NC.ITEMS_PERPAGE,
            CURRENT_PAGE: 1,
            startDate: '',
            endDate: '',
            FromDate: HF.getFirstDateOfMonth(),
            ToDate: new Date(),
            UserReportList: [],
            Keyword: '',
            sortField: 'date',
            isDescOrder: false,
            SelectedState: { value: '', label: "All" },
            SelectedFixture: { value: 0, label: "Select Fixture" },
            SelectedContest: { value: 0, label: "Select Contest" },
            SelectedReportType: { label: "Gst Report", value: "tax_invoice" },
            SeletedStateType: { label: "All", value: "" },
            PaymentType: [],
            TotalDeposit: '',
            posting: false,
            GstStateList: [],
            GstFixture: [],
            GstContest: [],
            GstReportList: [],
            TableFields: [],
            TotalCount: [],
        }
        this.SearchCodeReq = _debounce(this.SearchCodeReq.bind(this), 500);
    }
    componentDidMount() {
        if (HF.allowGst() != '1') {
            notify.show(MODULE_NOT_ENABLE, 'error', 5000)
            this.props.history.push('/dashboard')
        }
        this.getGstReport()
        this.getStateList()
        this.getFixture()
    }

    getGstReport = () => {
        this.setState({ posting: true })

        let { PERPAGE, CURRENT_PAGE, Keyword, FromDate, ToDate, sortField, isDescOrder, SelectedState, SelectedContest, SelectedFixture, SelectedReportType, TableFields, SeletedStateType } = this.state

        // for (var key in TableFields) {
        //     if (TableFields[key] == '') {
        //         notify.show(key.replace(/_/g, ' ').toUpperCase() + ' field can not be empty.', 'error', 3000)
        //         return false;
        //     }
        //     else {
        //         this.setState({
        //             updatePosting: false,
        //             TableFields: TableFields,
        //         })
        //     }
        // }

        let params = {
            items_perpage: PERPAGE,
            total_items: 0,
            current_page: CURRENT_PAGE,
            sort_order: isDescOrder ? "ASC" : 'DESC',
            sort_field: sortField,
            csv: false,
            from_date: FromDate ? moment(FromDate).format("YYYY-MM-DD") : '',
            to_date: ToDate ? moment(ToDate).format("YYYY-MM-DD") : '',
            keyword: Keyword,
            report_type: SelectedReportType.value,
            state: SelectedState.value,
            contest_id: SelectedContest.value,
            season_game_uid: SelectedFixture.value,
            state_type: SeletedStateType.value,
        }

        WSManager.Rest(NC.baseURL + NC.GET_GST_REPORT, params).then(ResponseJson => {
            if (ResponseJson.response_code == NC.successCode) {
                // if ((ResponseJson.data != "No Record")) {
                this.setState({
                    posting: false,
                    GstReportList: !_isUndefined(ResponseJson.data.result) ? ResponseJson.data.result : [],
                    TableFields: ResponseJson.data.table_field,
                    TotalCount: ResponseJson.data.total_count,
                    TotalUser: ResponseJson.data.total,
                })
                // }

            } else {
                notify.show(NC.SYSTEM_ERROR, "error", 3000)
            }
        }).catch(error => {
            notify.show(NC.SYSTEM_ERROR, "error", 3000)
        })
    }

    getStateList = () => {
        this.setState({ posting: true })
        let params = {
            'master_country_id': 101,
            state_type: this.state.SeletedStateType.value,
        }
        WSManager.Rest(NC.baseURL + NC.GET_STATE_LIST, params).then(ResponseJson => {
            if (ResponseJson.response_code == NC.successCode) {
                const Temp = [{ value: '', label: "All" }]
                _Map(ResponseJson.data.state_list, (item, idx) => {
                    Temp.push({
                        value: item.master_state_id, label: item.state_name
                    })
                })
                this.setState({
                    GstStateList: Temp,
                })
            }
            else {
                notify.show(NC.SYSTEM_ERROR, "error", 3000)
            }
        }).catch(error => {
            notify.show(NC.SYSTEM_ERROR, "error", 3000)
        })
    }

    getFixture = () => {
        const { PERPAGE, CURRENT_PAGE, FromDate, ToDate } = this.state

        let params = {
            items_perpage: PERPAGE,
            current_page: CURRENT_PAGE,
            from_date: FromDate ? moment(FromDate).format("YYYY-MM-DD") : '',
            to_date: ToDate ? moment(ToDate).format("YYYY-MM-DD") : '',
        }
        WSManager.Rest(NC.baseURL + NC.GET_COMPLETED_FIXTURE, params).then(ResponseJson => {
            if (ResponseJson.response_code == NC.successCode) {
                let TempGstFixture = [{ 'value': '', 'label': 'Select Fixture' }];

                _Map(ResponseJson.data.result, (lObj, lKey) => {
                    let d = moment(new Date(WSManager.getUtcToLocal(lObj.season_scheduled_date)));
                    TempGstFixture.push({ value: lObj.season_game_uid, label: lObj.home + ' VS ' + lObj.away + ' (' + d.format("DD-MM-YYYY h:mm A") + ')', season_scheduled_date: lObj.season_scheduled_date });

                })
                this.setState({
                    GstFixture: TempGstFixture

                })
            } else {
                notify.show(NC.SYSTEM_ERROR, "error", 3000)
            }
        }).catch(error => {
            notify.show(NC.SYSTEM_ERROR, "error", 3000)
        })
    }

    getContest = () => {
        this.setState({ posting: true })
        let { SelectedFixture } = this.state
        let params = {
            season_game_uid: SelectedFixture.value,
        }

        WSManager.Rest(NC.baseURL + NC.GET_COMPLETED_CONTEST, params).then(ResponseJson => {
            if (ResponseJson.response_code == NC.successCode) {
                const TempGstContest = []
                _Map(ResponseJson.data, (item, idx) => {
                    TempGstContest.push({
                        value: item.contest_id, label: item.contest_name
                    })
                })
                this.setState({
                    GstContest: TempGstContest,
                })
            }
            else {
                notify.show(NC.SYSTEM_ERROR, "error", 3000)
            }
        }).catch(error => {
            notify.show(NC.SYSTEM_ERROR, "error", 3000)
        })
    }

    exportGstReport = () => {
        let { Keyword, FromDate, ToDate, sortField, isDescOrder, SelectedState, SelectedContest, SelectedFixture, SelectedReportType, SeletedStateType } = this.state

        let tempFromDate = ''
        let tempToDate = ''
        if (FromDate != '' && ToDate != '') {
            tempFromDate = moment(FromDate).format("YYYY-MM-DD");
            tempToDate = moment(ToDate).format("YYYY-MM-DD");
        }

        let ord = isDescOrder ? "ASC" : 'DESC'
        var query_string = 'csv=true&keyword=' + Keyword + '&from_date=' + tempFromDate + '&to_date=' + tempToDate + '&report_type=' + SelectedReportType.value + '&sort_field=' + sortField + '&sort_order=' + ord + '&state_type=' + SeletedStateType.value + '&season_game_uid=' + SelectedFixture.value + '&contest_id=' + SelectedContest.value + '&state=' + SelectedState.value;

        var export_url = 'adminapi/index.php/gst/gst_report?';

        HF.exportFunction(query_string, export_url)
    }

    downloadGstReport = (item) => {
        var query_string = '';

        query_string = 'user_id=' + item.user_id + '&lineup_master_contest_id=' + item.lineup_master_contest_id;

        let sessionKey = WSManager.getToken();
        query_string += "&Sessionkey" + "=" + sessionKey;

        window.open(NC.baseURL + 'adminapi/index.php/gst/gst_invoice_download?' + query_string, '_blank');
    }

    exportReport = () => {
        const { Keyword, FromDate, ToDate, sortField, isDescOrder } = this.state
        let params = {
            sort_order: isDescOrder ? "ASC" : 'DESC',
            sort_field: sortField,
            from_date: FromDate,
            to_date: ToDate,
            keyword: Keyword,
            report_type: "user_deposit"
        }

        WSManager.Rest(NC.baseURL + NC.EXPORT_REPORT, params).then(ResponseJson => {
            if (ResponseJson.response_code == NC.successCode) {
                notify.show(ResponseJson.message, "success", 5000);
            } else {
                notify.show(NC.SYSTEM_ERROR, "error", 3000)
            }
        }).catch(error => {
            notify.show(NC.SYSTEM_ERROR, "error", 3000)
        })
    }

    handleTypeChange = (value, name) => {
        if (value != null)
            this.setState({
                [name]: value,
                FromDate: new Date(Date.now() - 15 * 24 * 60 * 60 * 1000),
                ToDate: new Date(),
                Keyword: '',
                isDescOrder: false,
                sortField: 'date',
                SeletedStateType: { label: "StateType Select", value: "" },
                SelectedState: { value: 0, label: "Select State" },
                SelectedFixture: { value: 0, label: "Select Fixture" },
                SelectedContest: { value: 0, label: "Select Contest" },
            }, this.clearVariables)
    }

    handleTypeState = (value, name) => {
        if (value != null)
            this.setState({
                [name]: value,
                SelectedState: '',

            }, () => {
                this.clearVariables()
                this.getStateList()
            })
    }

    handleStateChange = (value, name) => {
        this.setState({ GstReportList: [] })

        if (value != null)
            this.setState({
                [name]: value,
            }, this.clearVariables)
    }

    handleFixtureChange = (value, name) => {
        this.setState({ GstContest: [] })
        if (value != null)
            this.setState({
                [name]: value,
                SelectedContest: '',
            }, () => {
                this.getContest();
                this.clearVariables();
            })
    }

    handleContestChange = (value, name) => {
        if (value != null)
            this.setState({ [name]: value }, this.clearVariables)
    }


    handleDateFilter = (date, dateType) => {
        this.setState({ [dateType]: date }, () => {
            this.clearVariables()
            this.getFixture()
        })
    }

    handlePageChange(current_page) {
        if (current_page != this.state.CURRENT_PAGE) {
            this.setState({
                CURRENT_PAGE: current_page
            }, () => {
                this.getGstReport();
            });
        }
    }

    searchByUser = (e) => {
        this.setState({ Keyword: e.target.value, CURRENT_PAGE : 1 }, this.SearchCodeReq)
    }

    SearchCodeReq() {
        if (this.state.Keyword.length > 2 || this.state.Keyword.length == 0)
            this.getGstReport()
    }

    clearFilter = () => {
        this.setState({
            FromDate: HF.getFirstDateOfMonth(),
            ToDate: new Date(),
            Keyword: '',
            isDescOrder: false,
            sortField: 'date',
            // SelectedReportType: { label: "Gst Report", value: "tax_invoice" },
            SeletedStateType: { label: "StateType Select", value: "" },
            SelectedState: { value: 0, label: "Select State" },
            SelectedFixture: { value: 0, label: "Select Fixture" },
            SelectedContest: { value: 0, label: "Select Contest" },
        }, () => {
            this.getGstReport()
        }
        )
    }

    clearVariables = () => {
        this.setState({
            CURRENT_PAGE: 1,
            GstReportList: [],
        }, this.getGstReport)
    }

    getSortCond = (skey) => {
        let keyArr = ['date', 'contest_name', 'entry_fee', 'platform_fee']
        if (keyArr.indexOf(skey) >= 0) {
            return true
        } else {
            return false
        }
    }

    sortContest(sortfiled, isDescOrder) {
        let Order = (sortfiled == this.state.sortField) ? !isDescOrder : isDescOrder

        this.setState({
            sortField: sortfiled,
            isDescOrder: Order,
            CURRENT_PAGE: 1,
        }, () => {
            this.getGstReport()
        })
    }

    render() {
        const { CURRENT_PAGE, PERPAGE, TotalUser, Keyword, GstStateList, GstFixture, GstContest, GstReportList, TableFields, TotalCount, SelectedFixture, SelectedContest, SelectedState, SelectedReportType, SeletedStateType, posting, isDescOrder } = this.state
        return (
            <Fragment>
                <div className="animated fadeIn mt-4">
                    <Row>
                        <Col md={12}>
                            <h1 className="h1-cls">
                                {SelectedReportType.label}
                            </h1>
                        </Col>
                    </Row>
                    <div className="user-deposit-amount">
                        <Row className="xfilter-userlist mt-5">
                            <Col md={2}>
                                <div>
                                    <label className="filter-label">Report Type</label>
                                    <Select
                                        isSearchable={true}
                                        class="form-control"
                                        options={ReportType}
                                        menuIsOpen={true}
                                        value={SelectedReportType}
                                        onChange={e => this.handleTypeChange(e, 'SelectedReportType')}
                                    />
                                </div>
                            </Col>
                            <Col md={2}>
                                <div className="search-box">
                                    <label className="filter-label">Search User</label>
                                    <Input
                                        placeholder="Search User"
                                        name='code'
                                        value={Keyword}
                                        onChange={this.searchByUser}
                                    />
                                </div>
                            </Col>
                            <Col md={2}>
                                <div>
                                    <label className="filter-label">From Date</label>
                                    <DatePicker
                                        maxDate={new Date(this.state.ToDate)}
                                        className="form-control"
                                        showYearDropdown='true'
                                        selected={this.state.FromDate}
                                        onChange={e => this.handleDateFilter(e, "FromDate")}
                                        placeholderText="From"
                                        dateFormat={NC.DATE_FORMAT}
                                    />
                                </div>
                            </Col>
                            <Col md={2}>
                                <div>
                                    <label className="filter-label">To Date</label>
                                    <DatePicker
                                        minDate={new Date(this.state.FromDate)}
                                        maxDate={new Date()}
                                        className="form-control"
                                        showYearDropdown='true'
                                        selected={this.state.ToDate}
                                        onChange={e => this.handleDateFilter(e, "ToDate")}
                                        placeholderText="To"
                                        dateFormat={NC.DATE_FORMAT}
                                    />
                                </div>
                            </Col>
                        </Row>
                        <Row className="xfilter-userlist mt-5">
                            {
                                SelectedReportType.value === 'tax_invoice' &&
                                <Fragment>
                                    <Col md={2}>
                                        <div className="search-box">
                                            <label className="filter-label">State Type</label>
                                            <Select
                                                isSearchable={true}
                                                class="form-control"
                                                options={StateType}
                                                menuIsOpen={true}
                                                value={SeletedStateType}
                                                onChange={e => this.handleTypeState(e, 'SeletedStateType')}
                                            />
                                        </div>
                                    </Col>
                                    <Col md={2}>
                                        {
                                            SeletedStateType.value == 'intra_state' &&
                                            <div className="overlap-state">

                                            </div>
                                        }

                                        <div className="search-box">
                                            <label className="filter-label">State Filter</label>
                                            <Select
                                                isSearchable={true}
                                                class="form-control"
                                                options={GstStateList}
                                                menuIsOpen={true}
                                                value={SelectedState}
                                                onChange={e => this.handleStateChange(e, 'SelectedState')}
                                                isOptionDisabled={true}

                                            />
                                        </div>
                                    </Col>
                                </Fragment>
                            }
                            <Col md={2}>
                                <div>
                                    <label className="filter-label">Fixture</label>
                                    <Select
                                        isSearchable={true}
                                        class="form-control"
                                        options={GstFixture}
                                        menuIsOpen={true}
                                        value={SelectedFixture}
                                        onChange={e => this.handleFixtureChange(e, 'SelectedFixture')}
                                    />
                                </div>
                            </Col>
                            <Col md={2}>
                                <div>
                                    <label className="filter-label">Contest</label>
                                    <Select
                                        isSearchable={true}
                                        class="form-control"
                                        options={GstContest}
                                        menuIsOpen={true}
                                        value={SelectedContest}
                                        onChange={e => this.handleContestChange(e, 'SelectedContest')}
                                    />
                                </div>
                            </Col>
                            <Col md={3} className="mb-5 mt-4">
                                <div className="filters-area">
                                    <Button className="btn-secondary" onClick={() => this.clearFilter()}>Clear Filters</Button>
                                </div>
                            </Col>
                            <Col md={1} className="mt-4">
                                <div className="">
                                    <i className={`export-list icon-export ${(!_isEmpty(GstReportList) && (GstReportList.length > 0)) ? '' : 'cls-dis'}`}
                                        onClick={e => (!_isEmpty(GstReportList) && (GstReportList.length > 0)) ? this.exportGstReport() : null}></i>

                                </div>
                            </Col>
                        </Row>
                        <Row>
                            <Col md={12} className="table-responsive common-table gst-table">
                                <Table>
                                    <thead>
                                        <tr>
                                            {
                                                _Map(TableFields, (item, idx) => {
                                                    let callback = this.getSortCond(item)
                                                    return (
                                                        <th
                                                            key={idx}
                                                            className={`${callback ? 'pointer' : ''}`}
                                                            onClick={() => callback ? this.sortContest(item, isDescOrder) : null}
                                                        >
                                                            {item === 'hsn_code' && 'HSN/SAC CODE'}
                                                            {item !== 'hsn_code' && item.replace(/_/g, ' ').toUpperCase()}
                                                        </th>
                                                    )
                                                })
                                            }
                                        </tr>
                                    </thead>
                                    <tbody>
                                        {
                                            (!_isEmpty(GstReportList) && (GstReportList.length > 0)) ?
                                                <React.Fragment>
                                                    {
                                                        _Map(GstReportList, (item, ind) => {
                                                            return (
                                                                <tr key={ind}>
                                                                    {
                                                                        _Map(TableFields, (fieldname, idx) => {
                                                                            let callback = this.getSortCond(fieldname)
                                                                            return (

                                                                                <td key={idx}>
                                                                                    {
                                                                                        fieldname === 'date' &&
                                                                                        HF.getFormatedDateTime(item[fieldname], 'DD-MM-YYYY')
                                                                                    }
                                                                                    {
                                                                                        fieldname !== 'date' &&
                                                                                        item[fieldname]
                                                                                    }
                                                                                    <div className="edit-stats-btn">
                                                                                        {
                                                                                            (fieldname == 'download_report') &&
                                                                                            <a onClick={e => this.downloadGstReport(item)} className="pointer">Invoice</a>
                                                                                        }
                                                                                    </div>
                                                                                </td>
                                                                            )
                                                                        })
                                                                    }
                                                                </tr>
                                                            )
                                                        }
                                                        )
                                                    }

                                                    <tr>
                                                        <td colSpan={`${SelectedReportType.value === 'tax_invoice' ? "6" : ""}`}>
                                                        </td>
                                                        {
                                                            _Map(TotalCount, (item, idx) => {
                                                                return (
                                                                    <td key={idx}>
                                                                        {item}
                                                                    </td>
                                                                )
                                                            })
                                                        }
                                                        <td colSpan={`${SelectedReportType.value === 'tax_invoice' ? "1" : "12"}`}>
                                                        </td>
                                                    </tr>
                                                </React.Fragment>

                                                :
                                                <tr>
                                                    <td colSpan='22'>
                                                        {((GstReportList.length == 0) && !posting) ?
                                                            <div className="no-records">No Record Found.</div>
                                                            :
                                                            <Loader />
                                                        }
                                                    </td>
                                                </tr>
                                        }

                                    </tbody>

                                </Table>
                            </Col>
                        </Row>
                        {TotalUser > PERPAGE && (
                            <div className="custom-pagination lobby-paging">
                                <Pagination
                                    activePage={CURRENT_PAGE}
                                    itemsCountPerPage={PERPAGE}
                                    totalItemsCount={TotalUser}
                                    pageRangeDisplayed={5}
                                    onChange={e => this.handlePageChange(e)}
                                />
                            </div>
                        )
                        }
                    </div>


                </div>
            </Fragment>
        )
    }
}
